      "title": "Software Engineer",
      "description": "\u003cp\u003eWe are an education technology company teaching kids fluent English all over the world.\nWe started as one man's vision to solve a simple problem for the students of his own school. In a little, over a decade we've grown into an international multimillion-dollar company positively affecting the lives of over 300,000 students a year.\u003c/p\u003e\n\u003ch1\u003eJob Description\u003c/h1\u003e\n\u003cp\u003eYour bread and butter of the day will be not just to code, but to engineer solutions to sometimes simple, most of the time complex problems.\nYou'll work closely with software designers, developers, and sometimes end-users, to understand more about what each product is meant to do, its key features, and who will use it.\nYou'll often be working on more than one project at a time, one product in the first phase of testing and another in its second or final phase.\u003c/p\u003e\n\u003ch1\u003eResponsibilities\u003c/h1\u003e\n\u003cp\u003eWhen you apply at a company you want to have some idea of the kinds of projects you may eventually help with, here is just a small list of what we're currently working on:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eDesign, build and maintain efficient, reusable, and reliable Ruby/Elixir code\u003c/li\u003e\n\u003cli\u003eIntegration of data storage solutions for MySQL, Postgres and CouchDB\u003c/li\u003e\n\u003cli\u003eIntegration of client apps for web, Android, and iOS, with server-side infrastructure.\u003c/li\u003e\n\u003cli\u003eIdentify bottlenecks and bugs, and devise solutions to these problems\u003c/li\u003e\n\u003cli\u003eHelp maintain code quality, organization, and automatization\u003c/li\u003e\n\u003c/ul\u003e\n\u003ch1\u003eSkills\u003c/h1\u003e\n\u003cul\u003e\n\u003cli\u003eExperience with Ruby on Rails, along with other common libraries\u003c/li\u003e\n\u003cli\u003eHands-on experience/interest on Elixir/Phoenix framework\u003c/li\u003e\n\u003cli\u003eGood understanding of the syntax of Ruby and its nuances\u003c/li\u003e\n\u003cli\u003eSolid understanding of object-oriented programming\u003c/li\u003e\n\u003cli\u003eGood understanding of server-side templating languages such as Haml\u003c/li\u003e\n\u003cli\u003eGood understanding of server-side CSS preprocessors such as Sass\u003c/li\u003e\n\u003cli\u003eBasic understanding of front-end technologies, such as JavaScript, HTML5, and CSS3\u003c/li\u003e\n\u003cli\u003eFamiliarity with concepts of MVC, Mocking, ORM, and RESTful\u003c/li\u003e\n\u003cli\u003eA knack for writing clean, readable code\u003c/li\u003e\n\u003cli\u003eAbility to integrate multiple data sources and databases into one system\u003c/li\u003e\n\u003cli\u003eUnderstanding of fundamental design principles behind a scalable application\u003c/li\u003e\n\u003cli\u003eAble to create database schemas that represent and support business processes\u003c/li\u003e\n\u003cli\u003eAble to implement automated testing platforms and unit tests\u003c/li\u003e\n\u003cli\u003eProficient understanding of Git\u003c/li\u003e\n\u003cli\u003eFamiliarity with development aiding tools Bundler, Webpacker\u003c/li\u003e\n\u003cli\u003eFamiliarity with continuous integration\u003c/li\u003e\n\u003c/ul\u003e\n",
