      "title": "DevOps-Engineer in Automotive-IT",
      "description": "\u003cp\u003eDevOps-Engineer (m/w/d) für virtualisierte CO-Simulationsumgebungen in der Automotive-IT\nArbeitsort: Ehningen\u003c/p\u003e\n\u003cp\u003eWas Sie erwartet:\nDas Auto von morgen ist nicht mehr nur ein Fahrzeug, mit dem man schnell fahren kann, vielmehr wird es zum Kundenerlebnis. Es wird maßgeblich durch seine Software sowie die Performance der darunterliegenden IT bestimmt. Die komplexeren Softwarearchitekturen erfordern die Abkehr von klassischen Absicherungsmethoden hin zu skalierbaren, virtuellen Frameworks. Wir bei Bertrandt denken die Zukunft der Softwareabsicherung in der Automobilbranche komplett neu.\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eWerden Sie Teil der Entwicklung virtueller und hochautomatisierter CI/CD-Frameworks um die Herausforderungen von morgen lösen zu können.\u003c/li\u003e\n\u003cli\u003eIn dieser Position können Sie die spannende Vereinigung von Automobil- und IT-Welten nicht nur beobachten, sondern können dabei direkt mitwirken.\u003c/li\u003e\n\u003cli\u003eSie verantworten die Konzeptionierung, Implementierung und die Inbetriebnahme einzelner Bausteine für virtualisierte Testumgebungen, welche bis in die Cloud skalierbar sind.\u003c/li\u003e\n\u003cli\u003eAls Experte in Ihrem Fachgebiet geben Sie wertvolle technische Impulse. Gerne bieten wir Ihnen auch die Perspektive die fachliche Leitung eines Projektteams zu übernehmen.\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eWas Sie mitbringen:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eEin abgeschlossenes Studium der (techn.) Informatik, Software/Systems- Engineering, Elektrotechnik o.ä.\u003c/li\u003e\n\u003cli\u003eMehrjährige Berufserfahrung als DevOps-Engineer und sicherer Umgang mit gängigen DevOps-Tools wie Jenkins (CI/CD), Git, Jira sowie mit „Infrastructure as Code“-Implementierung (z.B. Ansible)\u003c/li\u003e\n\u003cli\u003eFundierte Programmierkenntnisse (z.B. in C, C++, C# oder Python)\u003c/li\u003e\n\u003cli\u003eTiefergehende Kenntnisse der Ethernet-Protokolle (TCP/IP, UDP, VLAN/VxLAN) sowie Routing und Switching\u003c/li\u003e\n\u003cli\u003ePraktische Erfahrung mit Virtualisierungsumgebungen wie VMware, Virtualbox und QEMU\u003c/li\u003e\n\u003cli\u003eErfahrung in der Entwicklung auf Linux-basierten Systemen sind von Vorteil\u003c/li\u003e\n\u003cli\u003eSie möchten Ihre Ideen einbringen und haben Spaß an der Lösung von komplexen Fragestellungen\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eWenn Sie auf der Suche nach neuen Herausforderungen sind, Lust haben sich und Ihre Fähigkeiten in ein hoch motiviertes Team einzubringen und in einem dynamischen Umfeld an anspruchsvollen Lösungen für die Fahrzeuge von morgen zu arbeiten, dann sollten wir uns kennenlernen.\u003c/p\u003e\n\u003cp\u003eWas wir können:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eVerantwortungsvolle Aufgaben\u003c/li\u003e\n\u003cli\u003ePatenmodell\u003c/li\u003e\n\u003cli\u003eFlexible Arbeitszeiten\u003c/li\u003e\n\u003cli\u003eEigenverantwortliches Arbeiten\u003c/li\u003e\n\u003cli\u003eFirmeninterne Kantine\u003c/li\u003e\n\u003cli\u003eKonzernweites \"Netzwerken\"\u003c/li\u003e\n\u003cli\u003eRabatte\u003c/li\u003e\n\u003cli\u003eMobiles Arbeiten\u003c/li\u003e\n\u003cli\u003eSabbatical\u003c/li\u003e\n\u003cli\u003eFlache Hierarchien\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eKontakt:\nSwetlana Befort\nTel.: +49 7034 656-5579\u003c/p\u003e\n",
