      "title": "Entwicklungsingenieur (m/w/d) AUTOSAR",
      "description": "\u003cp\u003eEntwicklungsingenieur (m/w/d) AUTOSAR\nArbeitsort: Ehningen\u003c/p\u003e\n\u003cp\u003eWas Sie erwartet:\nIn dieser Position haben Sie die Möglichkeit Ihren Beitrag zur Entwicklung der Fahrzeuge der Zukunft zu leisten\n- Sie sind verantwortlich für die Integration von AUTOSAR Standard Software zur Durchführung von Validierungsmaßnahmen\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eSie analysieren, dokumentieren die Software-Fehlerbilder und erstellen die Testfälle anhand von Testspezifikationen\u003c/li\u003e\n\u003cli\u003eEigenverantwortlich führen Sie die Software White- und Black-Box Tests im Umfeld von AUTOSAR Integrationen durch und wirken bei technischen Reviews mit\u003c/li\u003e\n\u003cli\u003eAußerdem sind Sie für die Erweiterung und Pflege von Entwicklungsumgebungen sowie die Validierung von AUTOSAR ECU Extrakten und Stacks zuständig\u003c/li\u003e\n\u003cli\u003eZu Ihren Aufgaben gehören ebenfalls die Evaluierung neuer AUTOSAR Features, die Inbetriebnahme und Debuggen von Prototyp-Steuergeräten sowie die Durchführung von Gateway Routing-Tests an Seriensteuergeräten\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eWas Sie mitbringen:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eEin Abgeschlossenes technisches Studium (bspw. Elektrotechnik oder technische Informatik)\u003c/li\u003e\n\u003cli\u003ePraktische Erfahrungen in der Embedded Softwareentwicklung sowie erste Kenntnisse im Bereich AUTOSAR\u003c/li\u003e\n\u003cli\u003eGute Programmierkenntnisse in C, C++, CAPL sowie Kenntnisse und Erfahrung in der Anwendung der Vector Toolkette (CANoe, etc.)\u003c/li\u003e\n\u003cli\u003eErste Erfahrungen mit dem AUTOSAR Konfigurationstool DaVinci wären vorteilhaft\u003c/li\u003e\n\u003cli\u003eAußerdem bringen Sie Interesse an der Elektrotechnik mit und zeigen gerne Eigeninitiative\u003c/li\u003e\n\u003cli\u003eSichere Sprachkenntnisse in Deutsch und Englisch bringen Sie ebenfalls mit\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eWerden Sie Teil unserer Software Community, in der Sie eigene Ideen einbringen und verwirklichen können!\nNeben einer abwechslungsreichen Projektlandschaft bieten wir Ihnen ebenso ein hohes Maß an Eigenverantwortung und die tatkräftige Unterstützung qualifizierter Teams.\nWenn Sie Lust haben, die Zukunft des Automobil mitzugestalten, dann sollten wir uns kennenlernen.\u003c/p\u003e\n\u003cp\u003eWas wir können:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eVerantwortungsvolle Aufgaben\u003c/li\u003e\n\u003cli\u003eIntensive Einarbeitung\u003c/li\u003e\n\u003cli\u003ePatenmodell\u003c/li\u003e\n\u003cli\u003eFlexible Arbeitszeiten\u003c/li\u003e\n\u003cli\u003eEigenverantwortliches Arbeiten\u003c/li\u003e\n\u003cli\u003eKonzernweites \"Netzwerken\"\u003c/li\u003e\n\u003cli\u003eTrainingsangebote\u003c/li\u003e\n\u003cli\u003eRabatte\u003c/li\u003e\n\u003cli\u003eMobiles Arbeiten\u003c/li\u003e\n\u003cli\u003eFlache Hierarchien\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eKontakt:\nSwetlana Befort\nTel.: +49 7034 656-5579\u003c/p\u003e\n\u003cp\u003eStellenanzeige teilen:\u003c/p\u003e\n",
