      "url": "https://jobs.github.com/positions/7d413dbd-631b-4e75-ad34-bae66d0e385d",
      "created_at": "Wed May 12 10:40:09 UTC 2021",
      "company": "Remote",
      "company_url": "http://www.remote.com",
      "location": "Anywhere",
      "title": "Senior Frontend Engineer",
      "description": "\u003cp\u003eRemote is THE global platform that enables companies to employ people anywhere in the world, taking away all legal and compliance complexities. Remote handles global payroll, benefits, taxes, and compliance for businesses big and small. Read more about \u003ca href=\"https://remote.com/how-it-works\"\u003e\u003cstrong\u003ehow it works\u003c/strong\u003e\u003c/a\u003e.\u003c/p\u003e\n\u003cp\u003eWe encourage folks from all ethnic groups, genders, sexuality, age and abilities to apply. We value kindness, ownership and team collaboration. You can check out our \u003ca href=\"https://remote.com/handbook\"\u003e\u003cstrong\u003epublic handbook\u003c/strong\u003e\u003c/a\u003e to learn more about our culture. If this job description resonates with you, we want to hear from you!\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eHow we work\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eRemote is fully remote (ah!) and we love \u003ca href=\"https://www.notion.so/Working-Async-80c01cd443ad4c77a8ceaef7c5fba5d0\"\u003e\u003cstrong\u003eWorking Async\u003c/strong\u003e\u003c/a\u003e. This means you get to do your own schedule no matter the timezone. The Remote Engineering team uses a simple approach to productivity and task management and you can read more about it in \u003ca href=\"https://www.notion.so/remotecom/The-Remote-Flow-4ccc721f6dde40e4b5c91dd8e47e77c7\"\u003e\u003cstrong\u003eThe Remote Flow\u003c/strong\u003e\u003c/a\u003e.\u003c/p\u003e\n\u003cp\u003eThe team you'll be joining: 35 engineers, spanning across Frontend, Backend, SRE and QA. We're organised into product teams composed with engineers, designers, product managers, operations and finance, each dedicated to achieving the same goals in a specific domain.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eRequirements\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003ePrevious experience working as Frontend Engineer/Developer for at least 4 years\u003c/li\u003e\n\u003cli\u003eSolid understanding of \u003cstrong\u003eJavascript ES6\u003c/strong\u003e\n\u003c/li\u003e\n\u003cli\u003eProficient with \u003cstrong\u003eReact\u003c/strong\u003e\n\u003c/li\u003e\n\u003cli\u003eExperience with \u003cstrong\u003eJest\u003c/strong\u003e and \u003cstrong\u003eReact Testing Library\u003c/strong\u003e or \u003cstrong\u003eEnzyme\u003c/strong\u003e in writing meaningful tests.\u003c/li\u003e\n\u003cli\u003eInterest in build pages with performance, accessibility and API design in mind\u003c/li\u003e\n\u003cli\u003eAttention to the details when implementing UI mockups made by our amazing design team.\u003c/li\u003e\n\u003cli\u003eIt's not required to have experience working remotely, but considered a plus.\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eExperience Bonus\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eNext.js\u003c/li\u003e\n\u003cli\u003eElixir/Phoenix\u003c/li\u003e\n\u003cli\u003eDocker\u003c/li\u003e\n\u003cli\u003eGitLab CI\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eBenefits\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eRemote work culture\u003c/li\u003e\n\u003cli\u003eYour family comes first! Unlimited paid \u003ca href=\"https://www.notion.so/Time-off-e2e71e5dcdef43ff82515d7c711ed4af\"\u003e\u003cstrong\u003etime off\u003c/strong\u003e\u003c/a\u003e.\u003c/li\u003e\n\u003cli\u003eThe equipment/tools you need to do your job well and comfortable\u003c/li\u003e\n\u003cli\u003eGood salary and company options\u003c/li\u003e\n\u003cli\u003eFlexible working hours\u003c/li\u003e\n\u003cli\u003eHome office setup\u003c/li\u003e\n\u003cli\u003eHealth insurance\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eApplication process\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e(async) Application is reviewed by your future manager (Engineering Manager)\u003c/li\u003e\n\u003cli\u003eInterview with the recruiter\u003c/li\u003e\n\u003cli\u003eInterview with the Engineering Manager\u003c/li\u003e\n\u003cli\u003e(async) Code exercise (async, no deadline, a very simple exercise)\nInterview with the team (no managers on the call, just for you to get a sense of who you'd be working with)\u003c/li\u003e\n\u003cli\u003e(async) Offer\u003c/li\u003e\n\u003c/ul\u003e\n",
      "how_to_apply": "\u003cp\u003ePlease apply at \u003ca href=\"https://grnh.se/f7ebb2123us\"\u003ehttps://grnh.se/f7ebb2123us\u003c/a\u003e\u003c/p\u003e\n",
