      "description": "\u003cp\u003eServe as a developer implementing enterprise web applications using API driven .NET technologies (C# / JavaScript / SQL Server ) to deliver products to the travel industry as part of ongoing business initiatives\nEnhance and support existing large, mission-critical .NET applications that support core functions of the business as needed\nAssist in uncovering and implementing ways to improve the level of customer service, quality, and efficiency of the company\nActively participate in architectural discussions\nDevelop new features and make modifications to existing features\nTest and debug applications to ensure client needs are met\u003c/p\u003e\n\u003cp\u003eMinimum Requirements and Qualifications\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eAbility to demonstrate programming skill expertise\u003c/li\u003e\n\u003cli\u003e4+ years of experience as a Software Analyst/Developer\u003c/li\u003e\n\u003cli\u003eExperience with agile development processes\u003c/li\u003e\n\u003cli\u003eExperience in the travel industry with travel industry suppliers\u003c/li\u003e\n\u003cli\u003eA solid understanding of .NET, C#, and API-based web technologies.  ASP.NET MVC, JavaScript, and jQuery\u003c/li\u003e\n\u003cli\u003eUnix / Linux fluency\u003c/li\u003e\n\u003cli\u003eSignificant Microsoft SQL Server experience is required, you must know SQL\u003c/li\u003e\n\u003cli\u003eGood written and verbal communication skills\u003c/li\u003e\n\u003cli\u003eMust be a motivated self-starter adept at working remotely\u003c/li\u003e\n\u003cli\u003eMust have a dedicated home office environment free of distractions and the ability to participate in voice and video communications\u003c/li\u003e\n\u003cli\u003eMust be able to thrive in a geographically dispersed environment\u003c/li\u003e\n\u003cli\u003eExcellent critical thinking and problem-solving skills\u003c/li\u003e\n\u003c/ul\u003e\n",
