      "title": "In- House Software Developer",
      "description": "\u003cp\u003eWe are looking for a passionate Software Engineer to design, develop and install solutions within our internal EHR system. Software Engineer responsibilities include gathering user requirements, defining system functionality, and writing code in various languages, like Java, or .NET programming languages (e.g. C++ or JScript.NET.). Our ideal candidates are familiar with the software development life cycle (SDLC) from preliminary system analysis to tests and deployment. Ultimately, the role of the Software Engineer is to build high-quality, innovative, and fully performing software that complies with coding standards and technical design.\u003c/p\u003e\n\u003cp\u003eResponsibilities\u003c/p\u003e\n\u003cp\u003eExperience with writing application software: Visual Studio, MSSQL Management Studio.\u003c/p\u003e\n\u003cp\u003eExperience with data analysis, data access, data structures, data manipulation, databases, design\nSQL Server DDBB and Report Services.\u003c/p\u003e\n\u003cp\u003eDevelop flowcharts, layouts and documentation to identify requirements and solutions with Information and Technology (I\u0026amp;T) standards\u003c/p\u003e\n\u003cp\u003eWrite well-designed, testable code\u003c/p\u003e\n\u003cp\u003eProduce specifications and determine operational feasibility\u003c/p\u003e\n\u003cp\u003eProgramming, testing, and implementation application in C#, JavaScript, HTML5, CSS\u003c/p\u003e\n\u003cp\u003eEnvironments include, but are not limited to application servers, ASP.NET MVC, JQuery, and Bootstrap\u003c/p\u003e\n\u003cp\u003eDemonstrate proficiencies in creating report programs. SQL Report Server, Develop new programs or help maintain existing programs\u003c/p\u003e\n\u003cp\u003eDevelop software verification plans and quality assurance procedures\u003c/p\u003e\n\u003cp\u003eDocument and maintain software functionality\u003c/p\u003e\n\u003cp\u003eTroubleshoot, debug and upgrade existing systems\u003c/p\u003e\n\u003cp\u003eDeploy programs and evaluate user feedback\u003c/p\u003e\n\u003cp\u003eComply with project plans and industry standards\u003c/p\u003e\n\u003cp\u003eEnsure software is updated with the latest features\u003c/p\u003e\n\u003cp\u003eSkills\u003c/p\u003e\n\u003cp\u003eProven work experience as a Software Engineer or Software Developer\u003c/p\u003e\n\u003cp\u003eExperience designing interactive applications\u003c/p\u003e\n\u003cp\u003eAbility to develop software in Java, Ruby on Rails, C++ or other programming languages\u003c/p\u003e\n\u003cp\u003eExcellent knowledge of relational databases, SQL and ORM technologies (JPA2, Hibernate)\u003c/p\u003e\n\u003cp\u003eExperience developing web applications using at least one popular web framework (JSF, Wicket, GWT, Spring MVC)\u003c/p\u003e\n\u003cp\u003eExperience with test-driven development\u003c/p\u003e\n\u003cp\u003eProficiency in software engineering tools\u003c/p\u003e\n\u003cp\u003eAbility to document requirements and specifications\u003c/p\u003e\n\u003cp\u003eBSc degree in Computer Science, Engineering or relevant field\u003c/p\u003e\n",
