      "url": "https://jobs.github.com/positions/1173fb11-176f-45cb-819e-25e1bd5984e2",
      "created_at": "Tue May 11 06:58:08 UTC 2021",
      "company": "uptodate Ventures",
      "company_url": "https://www.uptodate.de",
      "location": "Munich",
      "title": "Senior Software Engineer (m/f/d)",
      "description": "\u003cp\u003e\u003cstrong\u003eAbout the Position\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003e\"uptodate\", has been founded in cooperation with Versicherungskammer Bayern, is looking for support from a \"Senior Software Engineer (m/f/d)” as soon as possible. We are working to create an ecosystem around living through innovative solutions and smart technologies that make everyday life easier. At uptodate, an agile startup culture and a novel product is combined with a solid investor. If you are open for a new challenge, feel free to check out our career website.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eYour tasks\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eDesign and develop new products together with our development and project teams and bring existing solutions from MVP to maturity level\u003c/li\u003e\n\u003cli\u003eTake new features live quickly - but always with an eye on the big picture and quality\u003c/li\u003e\n\u003cli\u003eCode reviews and automated tests are part of your daily routine\u003c/li\u003e\n\u003cli\u003eDevelop our products \"mobile first\" and improve and maintain the CI/CD pipeline\u003c/li\u003e\n\u003cli\u003eWork in an agile Scrum team - in close collaboration with product owners and designers\u003c/li\u003e\n\u003cli\u003eYou will work with React, React Native, TypeScript, HTML5, CSS, AWS, serverless and much more. On our Uptodate Radar you can get a more detailed overview on how and with what tools we are working\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eYour profile\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eYou are used to work with complex systems eg. traffic, number of users, PII data\u003c/li\u003e\n\u003cli\u003eYou should have been exposed to serverless architecture, DEVOPS and cloud\u003c/li\u003e\n\u003cli\u003eWorking and teaching juniors is something you like to do\u003c/li\u003e\n\u003cli\u003eGood English communication skills in writing and speaking\u003c/li\u003e\n\u003cli\u003eYou always wanted to become a developer - because programming is your passion!\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eWhy us?\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eChallenging, varied and independent tasks\u003c/li\u003e\n\u003cli\u003eResponsibility right from the start - great creative freedom with regard to our products and processes\u003c/li\u003e\n\u003cli\u003eA great international team and collegial atmosphere with a lot of fun\u003c/li\u003e\n\u003cli\u003eAn open corporate culture with flat hierarchies, short decision-making processes and a hands-on mentality\u003c/li\u003e\n\u003cli\u003eRegular (virtual) team events\u003c/li\u003e\n\u003cli\u003eParticipation in corporate benefits of the Versicherungskammer such as fitness offers and canteen\u003c/li\u003e\n\u003cli\u003eFlexible working hours and home office arrangement (a location within Germany/outside of Munich is possible by arrangement)\u003c/li\u003e\n\u003cli\u003eA new, modern office with free drinks and fresh fruit\u003c/li\u003e\n\u003cli\u003eAttractive salary package and 30+ vacation days\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eSounds great? So does our 3-steps recruitment process, which takes around 2-3 weeks, is very transparent and simple - with detailed feedback at every stage if you wish. You want to be a part of uptodate? Please apply via one click on our website.\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://uptodate.join.com/jobs/2245962-senior-software-engineer-m-f-d?pid=357a3b4531918760973f\u0026amp;oid=b4295bc8-bf69-488e-8e73-c9fb44011609\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=senior%2Bsoftware%2Bengineer%2Bm%2Bf%2Bd\"\u003ehttps://uptodate.join.com/jobs/2245962-senior-software-engineer-m-f-d?pid=357a3b4531918760973f\u0026amp;oid=b4295bc8-bf69-488e-8e73-c9fb44011609\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=senior%2Bsoftware%2Bengineer%2Bm%2Bf%2Bd\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": "https://jobs.github.com/rails/active_storage/blobs/eyJfcmFpbHMiOnsibWVzc2FnZSI6IkJBaHBBc2lpIiwiZXhwIjpudWxsLCJwdXIiOiJibG9iX2lkIn19--4ba9eacef5cab513953242c024feaee6d961d19b/uptodate.jpg"
