      "description": "\u003cp\u003e\u003cstrong\u003eTasks\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eExciting penetration tests and security source code reviews at well-known companies.\u003c/li\u003e\n\u003cli\u003eWeb application, infrastructureand mobile penetration tests.\u003c/li\u003e\n\u003cli\u003eSocial engineering, physical pentests and red team assignments.\u003c/li\u003e\n\u003cli\u003eContribute to the creation of innovative security software for automated pentests.\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eRequirements\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eA degree in computer science or comparable training.\u003c/li\u003e\n\u003cli\u003eSound knowledge of agile software development.\u003c/li\u003e\n\u003cli\u003eHigh willingness to acquire and apply new knowledge.\u003c/li\u003e\n\u003cli\u003eGood command of English in order to communicate on an international level.\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eBenefits\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eChoice between remote (100% possible) or presence.\u003c/li\u003e\n\u003cli\u003eEvents in person, if the pandemic allows it.\u003c/li\u003e\n\u003cli\u003eConstant exchange in voice chat.\u003c/li\u003e\n\u003cli\u003eCritical feedback for personal and shared growth.\u003c/li\u003e\n\u003c/ul\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://turingpoint.join.com/jobs/2231376-penetration-tester?pid=357a3b4531918760973f\u0026amp;oid=e9b61f14-ddb5-4aad-8781-5c030dffa063\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=penetration%2Btester\"\u003ehttps://turingpoint.join.com/jobs/2231376-penetration-tester?pid=357a3b4531918760973f\u0026amp;oid=e9b61f14-ddb5-4aad-8781-5c030dffa063\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=penetration%2Btester\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": "https://jobs.github.com/rails/active_storage/blobs/eyJfcmFpbHMiOnsibWVzc2FnZSI6IkJBaHBBc2FpIiwiZXhwIjpudWxsLCJwdXIiOiJibG9iX2lkIn19--cc84cd4b85b0e02774bb3bd07924bc880fbfbf26/turing%20point.png"
