      "company_url": "http://WEX",
      "location": "United States",
      "title": "Senior Golang Developer (remote)",
      "description": "\u003cp\u003eWEX Inc. is a leading and growing global provider of payment processing, information management and fleet card payment solutions. We hire people who share our passion for continuous innovation and client service that is unparalleled in the industry.\u003c/p\u003e\n\u003cp\u003eWe're presenting individuals like you with an opportunity in which you can thrive in a dynamic, compassionate, and friendly work environment. We are Great Place To Work-Certified and we take it seriously!\u003c/p\u003e\n\u003cp\u003eWho Are We?\nWe’re the Transact Global (TAG) team at WEX.\u003c/p\u003e\n\u003cp\u003eTAG is an open loop payment processing platform built from the ground up by an agile team. It is cloud-native and built primarily with Go, MongoDB, and Terraform. The TAG team is pushing the frontiers of payments technology and is in the process of continuing to operationalize and scale the product.\u003c/p\u003e\n\u003cp\u003eOur team holds itself to a high-standard and we collaborate closely with one another to ensure strong, reliable and effective relationships all while maintaining a healthy work-life balance. We own our results and we take pride of ownership in everything we do.\u003c/p\u003e\n\u003cp\u003eWe need help!\nChanging the world isn’t easy, and we have a lot of work ahead of us. From new product features, to automation, to creating and supporting microservices, we’ve got more work than we can handle and we’re looking for great people to come along for the ride.\u003c/p\u003e\n\u003cp\u003eWho are you?\nCulturally, you’re:\n-A highly motivated engineer who loves working on small, high performing teams.\n-Collaborative, a solid communicator, and work well with your team and stakeholders.\n-Someone who cares deeply for team results, checks your ego at the door, and takes pride in owning results.\n-A mentor who is capable at guiding the technical development of less experienced developers at the functional, component architectural levels.\n-A professional who can advocate strongly for positions and still be 100% behind team decisions even if they don’t go your way.\n-Are comfortable balancing the need to move fast with the realities of working in a highly regulated space like payments.\u003c/p\u003e\n\u003cp\u003eTechnically, you:\n-Are a top-notch coder.\n-Pick up new technology and switch between tech stacks with a minimum of fuss.\n-Are comfortable working on front-end web code, back end services, data stores, and infrastructure systems.\n-Are a solid architect/designer/engineer.\n-Have experience writing modern software deployed in the cloud.\n-Are comfortable with encryption schemes, modern APIs, and front-end frameworks.\n-Have worked on agile teams to deliver software iteratively.\u003c/p\u003e\n\u003cp\u003eAt a minimum, you:\n-Have a BS in an engineering field OR can make us feel intensely confident that you don’t need one.\n-Have 8+ years of development experience.\n-Have 5+ years of experience developing internal/external web services.\n-A demonstrable working understanding of at least one modern web framework.\n-A demonstrable working understanding of NoSQL datastores.\u003c/p\u003e\n\u003cp\u003eIt would be nice if you:\n-Can show us one or more passion projects or open-source work you have contributed to in your own time.\n-Have experience with Golang, MongoDB, AWS, and Terraform.\n-Have demonstrable experience with systems engineering and automation.\n-Enjoy hanging out with your team.\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003eTo Apply, Click Here:\n\u003ca href=\"https://wexinc.wd5.myworkdayjobs.com/WEXInc/job/US---Remote/Senior-Golang-Developer--remote-_R3650\"\u003ehttps://wexinc.wd5.myworkdayjobs.com/WEXInc/job/US---Remote/Senior-Golang-Developer--remote-_R3650\u003c/a\u003e\u003c/p\u003e\n",
