      "company_url": "http:",
      "location": "SUNNYVALE, CA",
      "title": "Principal Software Engineer",
      "description": "\u003cp\u003ePosition Summary...\nWhat you'll do...\u003c/p\u003e\n\u003cp\u003eMarketplace Engineering team is in the forefront of building core platforms and services to enable Walmart to deliver vast selection at competitive prices and with best in class post order experience by enabling third party sellers to list, sell and manage their products to our customers on walmart.com. We do this with managing entire seller lifecycle, monitoring customer experience, and deliver high value insights to our sellers to help them plan their assortment, price, inventory. The team also actively collaborate with partner platform teams to ensure we continue to deliver the best experience to our sellers and our customers.\u003c/p\u003e\n\u003cp\u003eOur team is growing, and we are looking for a highly motivated Principal Software Engineer - Backend who will play a critical role in the design and implementation of some of our core applications that impacts the largest retailer in the world!\u003c/p\u003e\n\u003cp\u003eIn this role, you will lead and help our teams in driving down costs to implementation, championing continuous improvement, engineering excellence, innovation and mentoring engineers on developing and enhancing our enterprise scale software applications and frameworks.\u003c/p\u003e\n\u003cp\u003eYour job will include:\u003c/p\u003e\n\u003cp\u003eDevelop highly scalable services to process millions of requests per day and solve complex software systems problems by leveraging state-of-the-art technology\nGain exposure to various technologies used in Marketplace and E-commerce platforms\nDrive projects of high visibility across the organization\nLead team in designing systems and apps\nLeads team projects by reviewing project requirements, translating requirements into technical solutions while gathering requested information\nDesigning and developing backend microservices and REST APIs for web applications using JVM ecosystem (Kotlin, Java) and other languages and frameworks (Python, Go)\nTroubleshoot open issues and bug-fixes\nPartner and collaborate with project team and cross functional teams\nProvide leadership to the project team\nLead the discovery phase of projects to come up with high level design by partnering with the product management, project management, and business and user experience teams\nMust-Have Requirements:\u003c/p\u003e\n\u003cp\u003eOver 8 years of experience in architecting large scale systems with challenges of scalability and delivering sub-second latencies on 99.99% uptime\nMaster's degree in Computer Science or equivalent experience\nPractical knowledge of agile software development methodologies (e.g., XP, scrum)\nExperience in large scale distributed systems and event-driven architectures\nExperience and fluency with relational / non-relational databases (e.g. MySQL, Oracle, Cassandra, etc)\nStrong background in algorithms and data structures\nExpertise in one or more programming languages (e.g. Java, Python, etc)\nStrong inclination towards exploring and learning new technologies\nProven understanding of micro-services oriented architecture and extensible REST APIs\nUnderstanding of SaaS, PaaS, IaaS industry with hands on experience with public or private cloud offerings (e.g., AWS, GAE, Azure)\nAdvanced knowledge of performance, scalability, and system architecture with an eye toward avoiding and reducing technical debt\nUnderstanding of machine learning\u003c/p\u003e\n\u003cp\u003eNice To Have:\u003c/p\u003e\n\u003cp\u003ePrevious experience in the marketplace/e-commerce domain is a bonus but not a requirement.\u003c/p\u003e\n\u003cp\u003eBenefits \u0026amp; Perks: \u003c/p\u003e\n\u003cp\u003eBeyond competitive pay, you can receive incentive awards for your performance. Other great perks include 401(k) match, stock purchase plan, paid maternity and parental leave, PTO, multiple health plans, and much more. \u003c/p\u003e\n\u003cp\u003eEqual Opportunity Employer \u003c/p\u003e\n\u003cp\u003eWalmart, Inc. is an Equal Opportunity Employer – By Choice. We believe we are best equipped to help our associates, customers and the communities we serve live better when we really know them. That means understanding, respecting and valuing diversity- unique styles, experiences, identities, ideas and opinions – while being inclusive of all people. \u003c/p\u003e\n\u003cp\u003eWho We Are\u003c/p\u003e\n\u003cp\u003eJoin Walmart and your work could help over 275 million global customers live better every week. Yes, we are the Fortune #1 company. But you’ll quickly find we’re a company who wants you to feel comfortable bringing your whole self to work. A career at Walmart is where the world’s most complex challenges meet a kinder way of life. Our mission spreads far beyond the walls of our stores. Join us and you'll discover why we are a world leader in diversity and inclusion, sustainability, and community involvement.  From day one, you’ll be empowered and equipped to do the best work of your life. careers.walmart.com\u003c/p\u003e\n\u003cp\u003eMinimum Qualifications...\nOutlined below are the required minimum qualifications for this position. If none are listed, there are no minimum qualifications.\u003c/p\u003e\n\u003cp\u003eBachelor’s degree in Computer Science and 5 years’ experience in software engineering or related field OR 7 years’ experience in software\nengineering or related field\nPreferred Qualifications...\nOutlined below are the optional preferred qualifications for this position. If none are listed, there are no preferred qualifications.\u003c/p\u003e\n\u003cp\u003eMaster’s degree in Computer Science or related field and 4 years' experience in software engineering or related field\u003c/p\u003e\n\u003cp\u003ePrimary Location...\n840 W CALIFORNIA AVE, SUNNYVALE, CA 94086-4828, United States of America\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://careers.walmart.com/us/jobs/WD552828-principal-software-engineer?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\"\u003ehttps://careers.walmart.com/us/jobs/WD552828-principal-software-engineer?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\u003c/a\u003e\u003c/p\u003e\n",
