      "description": "\u003cp\u003ePosition Summary...\nWhat you'll do...\u003c/p\u003e\n\u003cp\u003eAs a part of the Core Services Retail and Emerging Tech Program Management team you will be working on some of our top initiatives, leading delivery across multiple agile teams across the organization.\u003c/p\u003e\n\u003cp\u003eYou’ll sweep us off our feet if…\u003c/p\u003e\n\u003cp\u003eYou are able to be both visionary and execute your vision\nYou are passionate, driven and humble in your approach\nYou enjoy working across organizations with a sense of ownership for the whole.\nYou’re excited about running an efficient operation delivering both quality \u0026amp; quantity\nYou’re customer-centric in spirit and in execution\nYou’re comfortable influencing others, leading team discussions, managing stakeholders, getting buy-in from leadership, and communicating clear.\nYou can de-risk complex initiatives into simpler clear next 3-5 steps.\nYou can create early wins for the teams.\nRespected by Technical Peers.\nPartnering, Communicating, and influencing across teams\nGet the right “stuff” done at the right time\nBite Sized Delivery grounded in Agile Principles\nLogical and Data Based Decision Making\nSets high quality standards and outcomes\nImplements structured frameworks for delivering work while empowering the team\nConstructively drives for improvement over time\nYou’ll make an impact by:\u003c/p\u003e\n\u003cp\u003eBringing Passion to ideate, plan, and ensure customer privacy is at the forefront of all we do\nConducting industry and competitive analysis – we aim to be best in class.\nPartnering with various stakeholders across the organization.\nDeveloping and implementing process and tools – you will manage the establishment of a new way of managing product imagery at Walmart.\nDefining vision and strategies for product content creation at Walmart, using domain expertise, internal and external best practices, identifying opportunities, building business cases, and gaining alignment in multiple internal and external stakeholder communities.\nBuilding collaborative relationships with key partners by driving priorities aligned to business goals, communicating and prioritizing implementation milestones, and gaining buy-in from executive leadership\nProviding overall direction by analyzing business objectives and customer needs\nBuilding a team environment by partnering to solve problems and staying focused till there is a solution\nDeveloping \u0026amp; Leading associates across multiple geographic locations.\nBeing Decisive regarding expanding or shutting parts of the imagery operation based on business need and efficiency.\nQualifications\u003c/p\u003e\n\u003cp\u003eBachelor's degree required/MBA a huge plus or equivalent in experience!\n6+ years in TPM\nPrevious Engineering/Tech Role a big Plus!\nTwo year of successful execution in a matrixed organization.\nMust be able and willing to think and act strategically and tactically.\nBenefits \u0026amp; Perks:\u003c/p\u003e\n\u003cp\u003eBeyond competitive pay, you can receive incentive awards for your performance. Other great perks include 401(k) match, stock purchase plan, paid maternity and parental leave, PTO, multiple health plans, and much more.\u003c/p\u003e\n\u003cp\u003eEqual Opportunity Employer\u003c/p\u003e\n\u003cp\u003eWalmart, Inc. is an Equal Opportunity Employer – By Choice. We believe we are best equipped to help our associates, customers and the communities we serve live better when we really know them. That means understanding, respecting and valuing diversity- unique styles, experiences, identities, ideas and opinions – while being inclusive of all people.\u003c/p\u003e\n\u003cp\u003eWho We Are?\u003c/p\u003e\n\u003cp\u003eJoin Walmart and your work could help over 275 million global customers live better every week. Yes, we are the Fortune #1 company. But you’ll quickly find we’re a company who wants you to feel comfortable bringing your whole self to work. A career at Walmart is where the world’s most complex challenges meet a kinder way of life. Our mission spreads far beyond the walls of our stores. Join us and you'll discover why we are a world leader in diversity and inclusion, sustainability, and community involvement.  From day one, you’ll be empowered and equipped to do the best work of your life. careers.walmart.com\u003c/p\u003e\n\u003cp\u003eMinimum Qualifications...\nOutlined below are the required minimum qualifications for this position. If none are listed, there are no minimum qualifications.\u003c/p\u003e\n\u003cp\u003eBachelor of Science and 5 years' technical program management experience OR Master of Science and 2 years' technical program management\nexperience OR 9 years' technical program management experience.\u003c/p\u003e\n\u003cp\u003ePreferred Qualifications...\nOutlined below are the optional preferred qualifications for this position. If none are listed, there are no preferred qualifications.\u003c/p\u003e\n\u003cp\u003ePrimary Location...\n600 WEST CALIFORNIA AVENUE, SUNNYVALE, CA 94086-2486, United States of America\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://careers.walmart.com/us/jobs/WD536927-senior-manager-i-technical-program-management-wmc?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\"\u003ehttps://careers.walmart.com/us/jobs/WD536927-senior-manager-i-technical-program-management-wmc?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": null
