      "created_at": "Fri May 07 16:31:26 UTC 2021",
      "company": "Walmart",
      "company_url": "http:",
      "location": "SUNNYVALE, CA",
      "title": "Senior Product Manager",
      "description": "\u003cp\u003ePosition Summary...\nWhat you'll do...\u003c/p\u003e\n\u003cp\u003eDo you like building products that catch bad actors? Do you like finding needles in haystacks? Love working with Big data and Machine Learning? Here is your opportunity!\u003c/p\u003e\n\u003cp\u003eWe are looking for a Tech Product Manager on the Anti-Fraud team who has the passion to work with data scientists and engineers to design and implement strategies that leverage Big Data and complex fraud detection algorithms to stop fraudsters in their tracks.\u003c/p\u003e\n\u003cp\u003eYour work will make e-commerce safer for millions of people and you will have a direct impact on the business goals of Fortune #1 company. This role also offers a unique opportunity to innovate and improve on ways to use Walmart’s ecomm, omni-channel products like Scan \u0026amp; Go, Walmart Stores and external data to solve problems. A successful candidate will have an established background in launching software or services, technical background or experience driving technical products, an entrepreneurial background, and a passion for great customer-centric online products.\u003c/p\u003e\n\u003cp\u003eQualifications:\u003c/p\u003e\n\u003cp\u003eMin of 4+ years of relevant work in technical product management, owning and driving end-to-end product requirements across multiple technology teams.\u003c/p\u003e\n\u003cp\u003eExperience in payments or retail or financial services industry as technical product manager.\u003c/p\u003e\n\u003cp\u003ePassion for technology, willingness to dive deep into the technical products Walmart is building, and commitment to learning and expanding technical knowledge\u003c/p\u003e\n\u003cp\u003eExperience partnering with Data Scientists and/or Machine learning engineers.\u003c/p\u003e\n\u003cp\u003eAn understanding of large-scale systems and service-oriented architecture\u003c/p\u003e\n\u003cp\u003eFraud, Risk, Payments, Identity/Account protection background\u003c/p\u003e\n\u003cp\u003eMachine learning product management experience\u003c/p\u003e\n\u003cp\u003eAbility to lead, drive consensus and deliver in a matrix organization with multiple stakeholders.\u003c/p\u003e\n\u003cp\u003eDemonstrated ability to communicate complex technical issues clearly and convincingly to a wide range of audiences., manage tradeoffs and evaluate opportunistic new ideas with internal and external partners.\u003c/p\u003e\n\u003cp\u003eDrive end-to-end integration with partner teams.\u003c/p\u003e\n\u003cp\u003eDemonstrated experience in analyzing large datasets and developing dashboards to derive insights. Comfortable with SQL or a programming language.\u003c/p\u003e\n\u003cp\u003eAbility to prioritize well, communicate clearly and understand how to drive a high level of focus and excellence with a strong, talented, opinionated team.\u003c/p\u003e\n\u003cp\u003eHigh attention to detail and proven ability to manage multiple, competing priorities simultaneously\u003c/p\u003e\n\u003cp\u003eBachelor's Degree in Computer Science or related field (advanced technical degree preferred)\u003c/p\u003e\n\u003cp\u003eMinimum Qualifications...\nOutlined below are the required minimum qualifications for this position. If none are listed, there are no minimum qualifications.\u003c/p\u003e\n\u003cp\u003eBachelor of Science and 5 years' product management experience OR Master of Science and 2 years' product management experience.\nPreferred Qualifications...\nOutlined below are the optional preferred qualifications for this position. If none are listed, there are no preferred qualifications.\u003c/p\u003e\n\u003cp\u003ePrimary Location...\n600 WEST CALIFORNIA AVENUE, SUNNYVALE, CA 94086-2486, United States of America\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://careers.walmart.com/us/jobs/WD515045-senior-product-manager-fraud-and-risk?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\"\u003ehttps://careers.walmart.com/us/jobs/WD515045-senior-product-manager-fraud-and-risk?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\u003c/a\u003e\u003c/p\u003e\n",
