      "title": "Senior Software Engineer",
      "description": "\u003cp\u003ePosition Summary...\nWhat you'll do...\u003c/p\u003e\n\u003cp\u003eIntroduction\u003c/p\u003e\n\u003cp\u003eIt’s exciting time to join our Walmart journey. We are seeking a Senior Software Engineer for a key position on Walmart’s continued initiative for Last Mile Delivery. Last Mile Delivery is the platform that powers Walmart’s Grocery and Same Day Delivery propositions. Last Mile Delivery is expected to grow multiple times over in the next few years. In this role, you will develop highly scalable services to process millions of requests per day and solve complex software engineering problems by leveraging state-of-the-art technology. You will drive projects of high visibility across the organization while leading the work of other small groups of six to ten engineers across geographies.\u003c/p\u003e\n\u003cp\u003eWhat you’ll bring…\u003c/p\u003e\n\u003cp\u003e5+ years of working experience in Java/J2EE\nDemonstrated high level design of systems\nSolid experience in large scale distributed systems and event-driven architecture.\nYou’ll sweep us off our feet if you…\u003c/p\u003e\n\u003cp\u003eHave designed, build, and maintain APIs, services, and systems\nHave experience with Kafka or other high volume-low latency messaging infrastructure\nHave experience with Azure Cloud Stack or other cloud stacks\nHave led or been part of modernizing tech stack or have migrated from on-prem to public cloud\nHave the knowledge of open-source libraries, design patterns and micro-service architecture. Familiarity with CI/CD and unit testing\nHave the knowledge of Kubernetes and other CNCF tools\nHave been Mentoring other team members\nLead others with respect\nAre curious and have the desire to learn\nWhat you’ll do…\u003c/p\u003e\n\u003cp\u003eLeads the work of other small groups of four to six engineers, including offshore associates, for assigned Engineering projects by proving pertinent documents, direction, and examples; identifying short-term and long-term solutions and timeline; reviewing and providing feedback for proposed solutions; and performing design and code reviews of changes.\nTroubleshoots business and production issues by reviewing and analyzing information (for example, issue, impact, criticality, possible root cause); engaging support teams to assist in the resolution of issues; formulating an action plan; directing actions as designated in the plan; interpreting the results to determine further action; performs root cause analysis to prevent future occurrence of issues; and completing online documentation.\nLeads and participates in medium- to large-scale, complex, cross-functional projects by reviewing project requirements; translating requirements into technical solutions; gathering requested information (for example, design documents, product requirements, wire frames); writing and developing code; conducting unit testing; communicating status and issues to team members and stakeholders; collaborating with project team and cross functional teams; troubleshooting open issues and bug-fixes; enhancing design to prevent re-occurrences of defects; ensuring on-time delivery and hand-offs; interacting with project manager to provide input on project plan; and providing leadership to the project team.\nAbout Global Tech\u003c/p\u003e\n\u003cp\u003eImagine working in an environment where one line of code can make life easier for hundreds of millions of people and put a smile on their face. That’s what we do at Walmart Global Tech. We’re a team of 15,000+ software engineers, data scientists and service professionals within Walmart, the world’s largest retailer, delivering innovations that improve how our customers shop and empower our 2.2 million associates. To others, innovation looks like an app, service or some code, but Walmart has always been about people. People are why we innovate, and people power our innovations. Being human-led is our true disruption.\u003c/p\u003e\n\u003cp\u003eWe’re virtual\u003c/p\u003e\n\u003cp\u003eWorking virtually this year has helped us make quicker decisions, remove location barriers across our global team, be more flexible in our personal lives and spend less time commuting.  Today, we are reimagining the tech workplace of the future by making a permanent transition to virtual work for most of our team. Of course, being together in person is an important part of our culture and shared success. We’ll collaborate in person at a regular cadence and with purpose.\u003c/p\u003e\n\u003cp\u003eMinimum Qualifications...\nOutlined below are the required minimum qualifications for this position. If none are listed, there are no minimum qualifications.\u003c/p\u003e\n\u003cp\u003eBachelor’s degree in Computer Science and 3 years’ experience in software engineering or related field OR 5 years’ experience in software\nengineering or related field.\nPreferred Qualifications...\nOutlined below are the optional preferred qualifications for this position. If none are listed, there are no preferred qualifications.\u003c/p\u003e\n\u003cp\u003eMaster’s degree in Computer Science or related field and 2 years' experience in software engineering or related field\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://careers.walmart.com/us/jobs/WD479267-senior-software-engineer?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\"\u003ehttps://careers.walmart.com/us/jobs/WD479267-senior-software-engineer?codes=GitHub\u0026amp;utm_source=GitHub\u0026amp;utm_campaign=Walmart\u0026amp;utm_medium=VanityURL\u0026amp;utm_term=Walmart_Tech\u0026amp;utm_content=Software_Development_and_Engineering\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": null
