      "title": "Staff Software Engineer – Supply Chain",
      "description": "\u003cp\u003ePosition Summary...\u003c/p\u003e\n\u003cp\u003eWhat you'll do...\u003c/p\u003e\n\u003cp\u003eAbout the team:\u003c/p\u003e\n\u003cp\u003eAt Walmart, our eCommerce success is powered by state-of-the-art supply chain capabilities, and now select Marketplace sellers can benefit from our expertise to grow their businesses. With Walmart Fulfillment Services, our sellers can focus on sales wile we expertly take care of fast shipping, seamless returns and customer services. Simply send your inventory to Walmart fulfillment centers, where we will store your products securely and prepare them swiftly for shipping when an order is placed.\u003c/p\u003e\n\u003cp\u003eAbout the role:\u003c/p\u003e\n\u003cp\u003eThe Staff Software Engineer will be working on problems related to scale and monetization through design, architecture and coding.\u003c/p\u003e\n\u003cp\u003eLeads the work of other small groups of six to ten engineers, including offshore associates, for assigned Engineering projects.\u003c/p\u003e\n\u003cp\u003eTroubleshoots business and production issues by reviewing and analyzing information (for example, issue, impact, criticality, possible root cause); engaging support teams to assist in the resolution of issues; formulating an action plan; directing actions as designated in the plan; interpreting the results to determine further action; performs root cause analysis to prevent future occurrence of issues; and completing online documentation.\u003c/p\u003e\n\u003cp\u003eLeads the discovery phase of medium to large projects to come up with high level design by partnering with the product management, Web Operations, project management, business and user experience teams; obtaining cross-function approvals; driving proof-of-concept; and implementing prototypes to validate ideas.\u003c/p\u003e\n\u003cp\u003eDevelops Innovation strategies, processes, and best practices by leading internal technical teams; partnering with cross-functional teams across the business; developing assessments of key opportunities; documenting project scopes; developing long-range plans and project timelines; communicating with and influencing decision-makers and executives within the organization; and resolving technology differences across teams through informed discussions.\u003c/p\u003e\n\u003cp\u003eLeads and participates in medium- to large-scale, complex, cross-functional projects by reviewing project requirements; translating requirements into technical solutions; gathering requested information (for example, design documents, product requirements, wire frames); writing and developing code; conducting unit testing; communicating status and issues to team members and stakeholders; collaborating with project team and cross functional teams; troubleshooting open issues and bug-fixes; enhancing design to prevent re-occurrences of defects; ensuring on-time delivery and hand-offs; interacting with project manager to provide input on project plan; and providing leadership to the project team.\u003c/p\u003e\n\u003cp\u003eSupports business objectives by collaborating with business partners to identify opportunities; addressing high-priority initiatives (for example, business strategy, technical feasibility, implementation alternatives); identifying short- and long-term solutions; and leading cross-functional partnership.\u003c/p\u003e\n\u003cp\u003eUtilizes industry research to improve Walmart's technology environment by analyzing industry best practices; bench marking industry against internal processes and solutions; researching or influencing future industry solutions for fit with internal needs; and defining software development guidelines, standards and processes.\u003c/p\u003e\n\u003cp\u003eProvides supervision and development opportunities for associates by selecting and training; mentoring; assigning duties; building a team-based work environment.\u003c/p\u003e\n\u003cp\u003ePromotes and supports company policies, procedures, mission, values, and standards of ethics and integrity by training and providing direction to others in their use and application; ensuring compliance with them; and utilizing and supporting the Open Door Policy.\u003c/p\u003e\n\u003cp\u003eEnsures business needs are being met by evaluating the ongoing effectiveness of current plans, programs, and initiatives.\u003c/p\u003e\n\u003cp\u003eYou will knock us off our feet if you have experience with:\u003c/p\u003e\n\u003cp\u003eJava, Python, Azure \u0026amp; GCP, Cosmos, Spark, Hadoop and Airflow\u003c/p\u003e\n\u003cp\u003eStrong programming and logical thinking\u003c/p\u003e\n\u003cp\u003eDesign and development of large scale applications\u003c/p\u003e\n\u003cp\u003eDistributed data processing \u0026amp; public cloud\u003c/p\u003e\n\u003cp\u003eMentoring other Engineers\u003c/p\u003e\n\u003cp\u003eYou will make impact by:\u003c/p\u003e\n\u003cp\u003eTaking the lead on problems related to scale \u0026amp; monetization\u003c/p\u003e\n\u003cp\u003eLeveraging your experience of designing and building large scale distributed systems, solving problems related to scale and have experience with process huge amounts of data\u003c/p\u003e\n\u003cp\u003eSharing your experiences with inboard transportation, outboard transportation, reverse logistics, payments and/or data platforms\u003c/p\u003e\n\u003cp\u003eAbout Global Tech:\u003c/p\u003e\n\u003cp\u003eImagine working in an environment where one line of code can make life easier for hundreds of millions of people and put a smile on their face. That’s what we do at Walmart Global Tech. We’re a team of 15,000+ software engineers, data scientists and service professionals within Walmart, the world’s largest retailer, delivering innovations that improve how our customers shop and empower our 2.2 million associates. To others, innovation looks like an app, service or some code, but Walmart has always been about people. People are why we innovate, and people power our innovations. Being human-led is our true disruption.\u003c/p\u003e\n\u003cp\u003eWe’re virtual\u003c/p\u003e\n\u003cp\u003eWorking virtually this year has helped us make quicker decisions, remove location barriers across our global team, be more flexible in our personal lives and spend less time commuting.  Today, we are reimagining the tech workplace of the future by making a permanent transition to virtual work for most of our team. Of course, being together in person is an important part of our culture and shared success. We’ll collaborate in person at a regular cadence and with purpose.\u003c/p\u003e\n\u003cp\u003eMinimum Qualifications...\nOutlined below are the required minimum qualifications for this position. If none are listed, there are no minimum qualifications.\u003c/p\u003e\n\u003cp\u003eBachelor’s degree in Computer Science and 4 years’ experience in software engineering or related field OR 6 years’ experience in software\nengineering or related field.\nPreferred Qualifications...\nOutlined below are the optional preferred qualifications for this position. If none are listed, there are no preferred qualifications.\u003c/p\u003e\n\u003cp\u003eMaster’s degree in Computer Science or related field and 3 years' experience in software engineering\u003c/p\u003e\n",
