      "title": "Cloud Native Architect",
      "description": "\u003cp\u003eWhat we do\u003c/p\u003e\n\u003cp\u003eThe International Committee of the Red Cross (ICRC) works worldwide to provide humanitarian assistance to people affected by conflict and armed violence. We take action in response to emergencies and at the same time promote respect for international humanitarian law. We are an independent and neutral organization, and our mandate stems essentially from the Geneva Conventions of 1949. We work closely with National Red Cross and Red Crescent Societies and with their International Federation in order to ensure a concerted, rational and rapid humanitarian response to the needs of the victims of armed conflict or any other situation of internal violence. We direct and coordinate the international activities conducted in these situations.\u003c/p\u003e\n\u003cp\u003ePurpose of the position\u003c/p\u003e\n\u003cp\u003eThe ICRC ICT division provides technology services to 20,000 employees globally, external partners and to the public. We run a broad range of enterprise tools, business-specific tools and tools directly designed for beneficiaries.\u003c/p\u003e\n\u003cp\u003eThe Cloud Native Architect is responsible for the architecture and development of native cloud applications aligned with DevOps principles according to requirements defined together with different business units.\u003c/p\u003e\n\u003cp\u003eThe Cloud Native Architect liaises with various ICT partners (internal customers, project managers, Solution Architects, platform owners and external actors) to participate in Application projects\u003c/p\u003e\n\u003cp\u003eIn addition, the Cloud Native Architect provides technical leadership across the organization, from strategic decision-making down to the project planning level.\u003c/p\u003e\n\u003cp\u003eMain duties and responsibilities\u003c/p\u003e\n\u003cp\u003eDefine architecture for cloud-native application development;\nSupport application migrations to cloud platforms;\nAccredited with relevant cloud certifications and enforcing standard methodologies and security built-in by default;\nContributes to the overall ICT cloud and architecture strategies and evolution;\nWork with the Solution Architects and Platform Owners to ensure the proper delivery of new services and the priority of all roadmap items.\u003c/p\u003e\n\u003cp\u003eRelationships\u003c/p\u003e\n\u003cp\u003eThe Cloud Native Architect will be working with the Head of the Applications Unit, interacting with ITC staff in other divisions as well as business units, and interacting externally with ICT cloud-related providers.\u003c/p\u003e\n\u003cp\u003eEducation and experience required\u003c/p\u003e\n\u003cp\u003eDegree in Computer Science, Information Systems or a similar study, or equivalent degree and/or experience;\nCloud Certification of Microsoft Azure (DP-200: Implementing an Azure Data Solution/ DP:201: Designing an Azure Data Solution/ AZ-303: Microsoft Azure Architect Technologies);\nAt least one certification in Agile methodology, ITIL or Software Architecture;\nExtensive knowledge of applications architecture and design patterns (API driven, Multi-layers and Micro-services);\nKnowledge of business ecosystems, SaaS, infrastructure as a service (IaaS), platform as a service (PaaS), SOA, Data flows (APIs, ETL, AMQP, etc.), open data, event-driven IT and predictive analytics;\nStrong DevOps technical background – CI/CD (Azure DevOps, TFS, Jenkins, TeamCity or others), Docker, Kubernetes, Ansible, Splunk;\nFamiliarity with information management practices, system development life cycle management, IT services management, infrastructure and operations, and EA and ITIL frameworks;\nGood knowledge of Enterprise Architecture software (for instance, “Erwin Casewise” suite);\nExposure and understanding of existing, new and emerging technologies, and processing environments.\u003c/p\u003e\n\u003cp\u003eDesired profile\u003c/p\u003e\n\u003cp\u003eExperience with orchestration and containerization deployment in private environments and/or in Cloud platforms (e.g. Azure, GCP);\nExperience designing cloud-native architectures;\nExperience with Agile methodologies is a plus;\nDeep knowledge in building and maintaining containers as well building CI/CD pipelines;\nExcellent time and task management skills as well as communication skills;\nOpen-minded, problem-solving and results oriented;\nCapable to work both within a team or independently to meet deadlines;\nAble to balance the long-term and short-term implications of individual decisions;\nAble to work with Platform Owners, other Solution Architects and Enterprise Architecture to ensure 2-way communication and guidelines enrichment on both sides.\u003c/p\u003e\n\u003cp\u003eDesired skills\u003c/p\u003e\n\u003cp\u003eAble to translate business needs into Software Enterprise Architecture (AA) requirements;\nAble to estimate the financial impact of AA alternatives;\nAble to apply multiple solutions to business problems and to support the architecture board to a quick and effective arbitration between them;\nAble to rapidly comprehend the functions and capabilities of new technologies;\nTrusted and respected as a leader who can influence and persuade business, IT leaders and platform owners;\nComfortable at working with business executives and technical teams, and able to push back in a professional and diplomatic way and drive consensus;\nHighly collaborative and supportive of business and of its ideals and strategies;\nVendor- and technology-neutral;\nExcellent command of English and French.\nAdditional information\u003c/p\u003e\n\u003cp\u003eLocation: Geneva\nType of contract: Open-ended contract\nGrade: C1\nActivity rate: 100%\nEstimated start date: ASAP\nApplication deadline: Sunday, the 6th of June 2021\u003c/p\u003e\n\u003cp\u003eIn line with the 2019-2022 ICRC Institutional Strategy to reach the goal of gender parity (50:50) among its managers by 2022 (and beyond); qualified female candidates are strongly encouraged to apply.\u003c/p\u003e\n\u003cp\u003eThe ICRC values diversity and is committed to creating an inclusive working environment. We welcome applications from all qualified candidates.\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003eGo to our career site here: \u003ca href=\"https://bit.ly/3eTL7eY\"\u003ehttps://bit.ly/3eTL7eY\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": null
