      "title": "Senior Frontend Engineer Angular 11+ (m/f/d)",
      "description": "\u003cp\u003ey42 is a Berlin-based SaaS company with a straightforward mission: Making it easy for everyone to use their data productively.\u003c/p\u003e\n\u003cp\u003eTo achieve this, we build a no-code business intelligence platform for loading, cleaning, connecting, visualizing and sharing data.\u003c/p\u003e\n\u003cp\u003ey42 was founded in 2020 and today already compiles a team of 30+ data witches and wizards, software magicians and business nerds from 10 nationalities. We are backed by international top-tier investors that deeply believe in our vision of frictionless business intelligence.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eTasks\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eNote: We prefer to hire for Berlin and Tirana. However, remote would be possible also with a stellar application.\u003c/p\u003e\n\u003cp\u003eIn this role, you will be responsible for building scalable frontend software together with a big team of Frontend Engineers. At the very core of our DNA, we are a product-driven company and believe that Frontend Development is one of the most important functions inside y42.\u003c/p\u003e\n\u003cp\u003eYou will work with a highly ambitious team that strives to build the world’s leading no-code business intelligence software and become the next European decacorn. Our Tech Stack: \u003ca href=\"https://stackshare.io/y42/y42\"\u003ehttps://stackshare.io/y42/y42\u003c/a\u003e\u003c/p\u003e\n\u003cp\u003eYou will:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eDevelop a scalable frontend architecture, using best practices to improve code maintainability and performance\u003c/li\u003e\n\u003cli\u003eHave a sense for UI/UX and understand the user journey on our platform\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eRequirements\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eThe role is perfect for you if you:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eHave 5+ years of experience in software engineering\u003c/li\u003e\n\u003cli\u003eAre experienced and in love with Angular 11+ and Typescript\u003c/li\u003e\n\u003cli\u003eWorked with RxJS, NgRx, ChangeDetection and OOP\u003c/li\u003e\n\u003cli\u003eAre ambitious and want to grow in an extremely fast-moving environment\u003c/li\u003e\n\u003cli\u003eHave designed, built and scaled production applications\u003c/li\u003e\n\u003cli\u003eHave a high degree of proactiveness, diligence and a get-stuff-done mentality\u003c/li\u003e\n\u003cli\u003eKnow best practices to produce high-quality code and deep understanding of object-oriented programming\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eBenefits\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eThe opportunity to significantly shape a VC-backed company in its early days\u003c/li\u003e\n\u003cli\u003e30% above market salary\u003c/li\u003e\n\u003cli\u003eWeekly team lunches and regular team events\u003c/li\u003e\n\u003cli\u003eA fully equipped office inside a vibrant community of other tech startups and innovators\u003c/li\u003e\n\u003cli\u003eState-of-the-art work equipment, incl. MacBook Pro, monitor and headphones of choice\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003ey42 is proud to be an equal opportunity employer prohibiting discrimination and harassment of any kind. We want to work with the best people no matter how they look, dress or speak, or whom they love, believe in, or aspire. We are committed to building a team that represents various backgrounds, perspectives, and skills, as we believe diverse teams with complementary skill sets make the strongest teams.\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://y42.join.com/jobs/2202759-senior-frontend-engineer-angular-11-m-f-d?pid=357a3b4531918760973f\u0026amp;oid=72edc007-e4a8-4939-b2b2-94ab0313ecf3\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=senior%2Bfrontend%2Bengineer%2Bangular%2B11%2Bm%2Bf%2Bd\"\u003ehttps://y42.join.com/jobs/2202759-senior-frontend-engineer-angular-11-m-f-d?pid=357a3b4531918760973f\u0026amp;oid=72edc007-e4a8-4939-b2b2-94ab0313ecf3\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=senior%2Bfrontend%2Bengineer%2Bangular%2B11%2Bm%2Bf%2Bd\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": "https://jobs.github.com/rails/active_storage/blobs/eyJfcmFpbHMiOnsibWVzc2FnZSI6IkJBaHBBckNpIiwiZXhwIjpudWxsLCJwdXIiOiJibG9iX2lkIn19--ccace7240b997c5dff66fbe16007832e07b1da7e/y-42-logo-xl.png"
