      "title": "Senior Productmanager (w/m/d) - Business Communication",
      "description": "\u003cp\u003e\u003cstrong\u003eWen suchen wir?\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eDu hast Spaß daran neue Business-Communication-Trends zu entdecken, ihr Potenzial zu bewerten und gemeinsam mit unserem Engineering daraus erfolgreiche Services zu machen? Du willst den Markt in den europäischen Märkten prägen und die Verantwortung für unsere Kernproduktlinie übernehmen? Dann freuen wir uns auf Deine Bewerbung als Senior Productmanager Business Communication (w/m/d) in Vollzeit oder Teilzeit.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eIhr Aufgabengebiet\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eGanzheitliche Verantwortung für unsere Business-Communication-Plattform\u003c/li\u003e\n\u003cli\u003eStrategische und operative Neu- und Weiterentwicklung über den gesamten Lebenszyklus\u003c/li\u003e\n\u003cli\u003eGestaltung und Priorisierung der Roadmap mit Blick auf zukünftige Marktpotenziale\u003c/li\u003e\n\u003cli\u003eAgile Produktentwicklung unserer Softwarelösungen und ergänzenden Portale gemeinsam mit den Product Ownern (w/m/d)\u003c/li\u003e\n\u003cli\u003eEnge Zusammenarbeit mit den Productmanagern (w/m/d) der anderen Produktlinien\u003c/li\u003e\n\u003cli\u003ePräsentation auf unserem STARFACE-Partnerkongress und ähnlichen Veranstaltungen\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eIhr Profil\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eErfahrung im Productmanagement im Umfeld digitaler Produkte\u003c/li\u003e\n\u003cli\u003eBereitschaft, aktiv Themen voranzutreiben und „Ownership“ zu übernehmen\u003c/li\u003e\n\u003cli\u003eFundierte IT-Kenntnisse (z.B. Datenbanken, Netzwerkprotokolle), gerne im Bereich der Telekommunikation/VoIP\u003c/li\u003e\n\u003cli\u003eErfahrung mit agilen Entwicklungsmethoden (z.B. Scrum, Kanban)\u003c/li\u003e\n\u003cli\u003eSehr gute Kommunikations- und Analysefähigkeiten\u003c/li\u003e\n\u003cli\u003eSehr gute Deutsch- und gute Englischkenntnisse in Wort und Schrift\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eIhr zukünftiger Arbeitsplatz\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eEin Unternehmen, das nicht nur vom Startup Charakter spricht, sondern ihn auch im täglichen Miteinander lebt\u003c/li\u003e\n\u003cli\u003eUnternehmensweite Transparenz und abteilungsübergreifende Kommunikation – wir verstehen uns als ein großes STARFACE Team\u003c/li\u003e\n\u003cli\u003eFlexible Arbeitszeiten, soweit keine Servicezeiten abzudecken sind\u003c/li\u003e\n\u003cli\u003eFlache Hierarchien, die im täglichen Miteinander nicht zu spüren sind. Wir führen Kommunikation auf Augenhöhe\u003c/li\u003e\n\u003cli\u003eWir haben keinen Dresscode, wir wollen, dass Sie sich an Ihrem Arbeitsplatz wohlfühlen und authentisch sind\u003c/li\u003e\n\u003cli\u003eViel Gestaltungspielraum für Ideen und Lösungen\u003c/li\u003e\n\u003cli\u003eBetriebliche Altersvorsorge, der obligatorische Obstkorb und Getränke verstehen sich von selbst\u003c/li\u003e\n\u003cli\u003eRegelmäßige Firmenfeiern und gerne auch an Freitagen ein Bier nach vier auf der Dachterrasse mit wunderschönem Blick über Karlsruhe\u003c/li\u003e\n\u003c/ul\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://starface.join.com/jobs/2196634-senior-productmanager-w-m-d-business-communication?pid=357a3b4531918760973f\u0026amp;oid=df156fa4-8798-4cd3-bdf9-f5def875971c\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=senior%2Bproductmanager%2Bw%2Bm%2Bd%2B-%2Bbusiness%2Bcommunication\"\u003ehttps://starface.join.com/jobs/2196634-senior-productmanager-w-m-d-business-communication?pid=357a3b4531918760973f\u0026amp;oid=df156fa4-8798-4cd3-bdf9-f5def875971c\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=senior%2Bproductmanager%2Bw%2Bm%2Bd%2B-%2Bbusiness%2Bcommunication\u003c/a\u003e\u003c/p\u003e\n",
