      "location": "Arezzo, Italy",
      "title": "Solution Architect",
      "description": "\u003cp\u003e\u003cstrong\u003eAbout the company\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eSintra with a turnover of 4.5 million Euros and about 100 employees in Italy, Poland and the UK, Sintra Digital Business has been operating in the web world for 19 years and has always offered solutions to grow the online business of its customers. Born from an entrepreneurial project, we have grown more and more to become a structured reality with over 200 B2B customers, 4,000 B2C customers and 70 employees in Italy and abroad.\u003c/p\u003e\n\u003cp\u003eThe candidate will be inserted within our team of developers constantly interacting with customers to understand their needs and collaborating with other team members implementing the already existing features and participating in innovative projects of web development.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eRole \u0026amp; Responsabilities\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eAnalyzing the technology environment\u003c/li\u003e\n\u003cli\u003eAnalyzing enterprise specifics\u003c/li\u003e\n\u003cli\u003eAnalyzing and documenting requirements\u003c/li\u003e\n\u003cli\u003eSetting the collaboration framework\u003c/li\u003e\n\u003cli\u003eCreating a solution prototype\u003c/li\u003e\n\u003cli\u003eParticipating in technology selection\u003c/li\u003e\n\u003cli\u003eControlling solution development\u003c/li\u003e\n\u003cli\u003eSupporting project management\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eRequirements\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eBachelor’s and/or master’s degree in technical disciplines\u003c/li\u003e\n\u003cli\u003eExperience in design and software development\u003c/li\u003e\n\u003cli\u003eAt least 5 years of IT work experience and at least 1 year of Cloud solution design and management experience\u003c/li\u003e\n\u003cli\u003eKnowledge of CI/CD tools (eg: Azure DevOps, Jenkins, Gitlab, Nexus)\u003c/li\u003e\n\u003cli\u003eKnowledge of the Devops methodology\u003c/li\u003e\n\u003cli\u003eBasic knowledge of Cloud Native development models and open source technologies\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eBenefits\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eFlexibility\u003c/li\u003e\n\u003cli\u003eBest Equipment\u003c/li\u003e\n\u003cli\u003eTraining budget\u003c/li\u003e\n\u003cli\u003ePaid leave\u003c/li\u003e\n\u003cli\u003eLearning opportunities\u003c/li\u003e\n\u003cli\u003eGrowth opportunities\u003c/li\u003e\n\u003cli\u003eImpact on the software and company\u003c/li\u003e\n\u003cli\u003eCutting edge technologies\u003c/li\u003e\n\u003cli\u003eOpportunity to Work with and Learn from Top Developers\u003c/li\u003e\n\u003cli\u003eSalary + Bonus\u003c/li\u003e\n\u003cli\u003eWell-being at Work\u003c/li\u003e\n\u003cli\u003eInternational Culture\u003c/li\u003e\n\u003c/ul\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://sintraconsulting.join.com/jobs/2230293-solution-architect?pid=24ab8afa01f084c85dce\u0026amp;oid=7e0cf02c-9c84-4bfd-8706-a46374dbb7d1\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=solution%2Barchitect\"\u003ehttps://sintraconsulting.join.com/jobs/2230293-solution-architect?pid=24ab8afa01f084c85dce\u0026amp;oid=7e0cf02c-9c84-4bfd-8706-a46374dbb7d1\u0026amp;utm_source=github_jobs\u0026amp;utm_medium=paid\u0026amp;utm_campaign=single%2Bposting\u0026amp;utm_content=solution%2Barchitect\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": "https://jobs.github.com/rails/active_storage/blobs/eyJfcmFpbHMiOnsibWVzc2FnZSI6IkJBaHBBcXFpIiwiZXhwIjpudWxsLCJwdXIiOiJibG9iX2lkIn19--81f07addb32bfdc7e48495589d96bbdabe588e88/sintra.jpg"
