      "title": "DevOps Engineer (Research Platforms)",
      "description": "\u003cp\u003eThe University of Oklahoma\nUniversity Libraries\nDevOps Engineer (Research Platforms)\nSalary Range: $60,000-$80,000 based on level of experience\u003c/p\u003e\n\u003cp\u003eThe University of Oklahoma Libraries needs a creative, mission-driven DevOps Engineer to join our Cloud Infrastructure \u0026amp; Development team. As a member of this team, you will apply a modern DevOps approach to the continuous improvement of operational practices, system reliability, and cost-effectiveness of a diverse portfolio of platforms and services supporting the library’s role as the intellectual crossroads of the university.\u003c/p\u003e\n\u003cp\u003eThis position focuses on the development and operation of our document repositories and library publishing platforms. Additionally, the position supports other key infrastructure, collaborating with colleagues across disciplines and organizational boundaries to plan, build, and operate components of the OU Libraries technology strategy.\u003c/p\u003e\n\u003cp\u003eTo see more detail about the Library DevOps Engineer (Library Publishing) position visit libraries.ou.edu/jobs. To apply for this position, go to \u003ca href=\"https://jobs.ou.edu\"\u003ehttps://jobs.ou.edu\u003c/a\u003e and search for job number 211234.\u003c/p\u003e\n\u003cp\u003eThe University of Oklahoma, in compliance with all applicable federal and state laws and regulations, does not discriminate on the basis of race, color, national origin, sex, sexual orientation, genetic information, gender identity, gender expression, age, religion, disability, political beliefs, or status as a veteran in any of its policies, practices or procedures. This includes, but is not limited to: admissions, employment, financial aid and educational services. For inquiries regarding non- discrimination policies, please visit the OU Institutional Equity Office.\u003c/p\u003e\n\u003cp\u003eApplicants must submit an ONLINE application at \u003ca href=\"http://www.jobs.ou.edu\"\u003ewww.jobs.ou.edu\u003c/a\u003e for the appropriate job requisition number. Computers and personal assistance are available at the Office of Human Resources, 905 Asp, Room 205, Norman, OK 73069. Include job requisition number on all correspondence. Deadlines subject to change with or without notice. For further information on this or other University of Oklahoma job opportunities, please call (405) 325-1826 or access our website at \u003ca href=\"http://www.hr.ou.edu\"\u003ewww.hr.ou.edu\u003c/a\u003e. The University of Oklahoma is an equal opportunity institution (\u003ca href=\"http://www.ou.edu/eoo\"\u003ewww.ou.edu/eoo\u003c/a\u003e).\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://jobs.ou.edu\"\u003ehttps://jobs.ou.edu\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": "https://jobs.github.com/rails/active_storage/blobs/eyJfcmFpbHMiOnsibWVzc2FnZSI6IkJBaHBBcUtpIiwiZXhwIjpudWxsLCJwdXIiOiJibG9iX2lkIn19--a1d52d67c1a1d46556f91354ae9172b728413d7b/OU_logo_web_360x180.jpg"
