      "location": "United States",
      "title": "Senior Software Engineer, Fullstack (U.S. Based Only, 100% Remote)",
      "description": "\u003cp\u003eWe’re looking for generalists who can dive in wherever needed, including frontend, backend, and some light data work. At our current stage, we’re light on process, so you should enjoy working with a high-level of autonomy and decision-making authority.\u003c/p\u003e\n\u003cp\u003eRead more \u003ca href=\"https://www.gethello.org/post/hello-worlds-unique-engineering-culture\"\u003ehttps://www.gethello.org/post/hello-worlds-unique-engineering-culture\u003c/a\u003e on Hello World's Unique Engineering Culture.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eYour Role\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eWrite code across our stack, including our mobile React Native clients and Rails backend\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eEnsure that our app works globally, including constrained environments such as low power devices or spotty connectivity\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eTackle challenging design and product problems\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eCollaborate with our CTO to architect and evolve a solution that scales. Our tech stack uses Typescript + React Native on the frontend with a light Rails + GraphQL backend running on App Engine. We're still early and nothing is set in stone, so there's lots of room to craft and improve our stack.\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eProvide thoughtful feedback to others on the team\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eHelp create a world-class engineering team and culture that is diverse from day one\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eSome examples of the problems we need your help to solve\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eHow do we showcase the talents of teens around the world in a simple “portfolio” to maximize their chances of finding opportunities?\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eHow can we ensure that uploading videos in our app is as reliable as uploads to YouTube in areas with metered data and poor connectivity?\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eHow can we rapidly build tools to allow scholarships and other opportunities to quickly identify promising teens on our platform?\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eYour Perks \u0026amp; Culture\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eWork on a product with impact, with a high level of autonomy, and solving problems for a global audience\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eWork on a team with a transparent pay structure with equal pay for equal roles (no bias, no behind-the-scenes negotiations)\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eReceive medical, dental, and vision + life insurance\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eUnlimited vacation and generous sick day policies\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eRemote-first organization\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eCompensation\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eThe salary for this role is $135,000\u003c/p\u003e\n\u003cp\u003eAt Hello World we celebrate and support our differences. We know employing a team rich in diverse thoughts, experiences, and opinions allows our employees, our product, and our community to flourish. Hello World is an equal opportunity workplace. We are dedicated to equal employment opportunities regardless of race, color, ancestry, religion, sex, national orientation, sexual orientation, age, citizenship, marital status, disability, gender identity, or Veteran status.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eYour Skills \u0026amp; Superpowers\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eExperience building mobile experiences (eg. React Native/Flutter/Native)\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003ePassion for validating new features by putting them in front of real users early and often\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eSome experience across both backend and frontend or a desire to learn\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eAbility to operate with a lot of autonomy, pragmatism, and a sense of craftsmanship\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eU.S. based and direct applicants will be considered (no recruiters please)\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n",
      "how_to_apply": "\u003cp\u003eApply on Hello World \u003ca href=\"https://helloworld.recruitee.com/o/senior-software-engineer-generalist\"\u003ehttps://helloworld.recruitee.com/o/senior-software-engineer-generalist\u003c/a\u003e\u003c/p\u003e\n",
      "company_logo": "https://jobs.github.com/rails/active_storage/blobs/eyJfcmFpbHMiOnsibWVzc2FnZSI6IkJBaHBBcFNpIiwiZXhwIjpudWxsLCJwdXIiOiJibG9iX2lkIn19--3e8db1a6561be2202382de66c7a3eb86bed5b096/linkedin%20profile-yellow.png"
