      "location": "Santa Monica, CA",
      "title": " Research Software Engineer",
      "description": "\u003cp\u003eThe RAND Corporation seeks an experienced Software Engineer to serve as the Senior Technical Programming Lead supporting RAND’s American Life Panel, the RAND Educator Panels, other online data collection services and other multidisciplinary software development activities.  The Senior Technical Programming Lead will work directly with a team of 3-4 research programmers involved in online survey software development and delivery.\u003c/p\u003e\n\u003cp\u003eThe position requires eliciting requirements from users, designing software products with researchers, implementing designs in code, and iterating with users and researchers to ensure functional and quality requirements are met. Qualified candidates will have demonstrated excellence in client engagement skills and experience recommending technical solutions for software and data migration projects.\u003c/p\u003e\n\u003cp\u003eThe selected candidate will work on project teams of research staff and domain experts and must be comfortable acting as the sole software engineer on the project. Technical needs will vary by project so the selected candidate must be a well-rounded generalist able to develop solutions in more than one of the following application paradigms:  desktop, web, mobile, database, modeling \u0026amp; simulation, big data analytics including machine learning, statistical analysis, and visualization.\u003c/p\u003e\n\u003cp\u003eDuties\u003c/p\u003e\n\u003cp\u003e•Proactive communication with project teams\n•Suggesting technical solutions and providing technical advice to the RAND American Life Panel Director and General Manager on how to best execute complex technical survey design projects.\n•Working closely with a small team of research programmers to program and field online surveys designed in Confirmit to collect primary research data for research purposes. In particular, this role will help implement more complex programming elements in survey designs (API connections, PHP, Javascript, etc.)\n•Developing in part or in whole interactive applications including the graphical user interface to back end server components and databases\n•Providing strategic technical inputs into computer programming and software integration, software transition/migration and software management activities\n•Developing applications or scripts to data mine, analyze, and visualize data sets\n•Briefing others on software design, software development progress, and software tool results\n•Providing programming time estimates for proposals and client budgets\n•Participating in software development QA activities\n•Writing documentation of software and software tool results for inclusion in customer briefings and RAND publications\u003c/p\u003e\n\u003cp\u003eBasic Qualifications\u003c/p\u003e\n\u003cp\u003e•Expertise with multiple web programming languages including JAVASCRIPT, PHP, or PYTHON\n•Expertise with at least one database platform such as MONGODB, MYSQL, or POSTGRES\n•Working knowledge of object oriented analysis and design\n•Some expertise in PYTHON, JAVA, C#, R,  C++, OR C\n•Willingness to pick up new technologies on a frequent basis\n•Software testing including unit, integration, and acceptance testing\n•Excellent written and verbal communication skills\n•Works best as part of a collaborative team\u003c/p\u003e\n\u003cp\u003eExperience with:\u003c/p\u003e\n\u003cp\u003e•Server management\n•Web application development\n•UI/UX Design\n•Cloud service providers like AWS or AZURE and tools in those ecosystems\n•Big data analytic platforms like HADOOP or SPARK\u003c/p\u003e\n\u003cp\u003ePreferred Qualifications\u003c/p\u003e\n\u003cp\u003e•Experience working in a research environment\n•Experience with Confirmit survey programming\n•Experience with system administration\u003c/p\u003e\n\u003cp\u003eEducation\u003c/p\u003e\n\u003cp\u003e•BA/BS required, MA/MS preferred, preferably in computer science or highly related field such as information systems, computer engineering, etc.\u003c/p\u003e\n\u003cp\u003eExperience\u003c/p\u003e\n\u003cp\u003e•6 years minimum relevant experience required.  8 or more years relevant experience preferred.\u003c/p\u003e\n\u003cp\u003eLocation\u003c/p\u003e\n\u003cp\u003eSanta Monica, Pittsburgh, Washington D.C., Boston\u003c/p\u003e\n\u003cp\u003eEqual Opportunity Employer: race/color/religion/sex/sexual orientation/gender identity/national origin/disability/vet\u003c/p\u003e\n",
      "how_to_apply": "\u003cp\u003e\u003ca href=\"https://rand.wd5.myworkdayjobs.com/en-US/External_Career_Site/job/Santa-Monica-CA-Greater-Los-Angeles-Area/Research-Software-Engineer_R539\"\u003ehttps://rand.wd5.myworkdayjobs.com/en-US/External_Career_Site/job/Santa-Monica-CA-Greater-Los-Angeles-Area/Research-Software-Engineer_R539\u003c/a\u003e\u003c/p\u003e\n",
