      "description": "\u003cp\u003e\u003cstrong\u003eWer wir sind\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eDie LORENZ Life Sciences Group ist weltweiter Marktführer für Softwarelösungen zum digitalen Austausch zwischen der pharmazeutischen Industrie und nationalen wie internationalen Behörden. Unsere Kunden vertrauen auf das Know-how und Engagement unserer rund 120 Mitarbeiterinnen und Mitarbeiter weltweit, die tagtäglich an der Schnittstelle zwischen Wissenschaft und Technologie erfolgreich agieren. Unsere Produkte steigern die Effizienz des Arzneimittelzulassungsprozesses und tragen unmittelbar dazu bei, notwendige Therapien für Patienten schneller zugänglich zu machen. \u003cstrong\u003eUnterstütze uns dabei, das Leben von Menschen, die auf neue Medikamente und Therapien angewiesen sind, zu verbessern!\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eWas Du bei uns machst\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eUnterstützung beim Aufbau und der Weiterentwicklung eines agilen DevOps-Teams\u003c/li\u003e\n\u003cli\u003eKonzeption und Automatisierung schneller, integrierter Prozesse für das bestmögliche Kundenerlebnis\u003c/li\u003e\n\u003cli\u003eDesign, Architektur und Dokumentation der zukünftigen Infrastruktur (Infrastructure as Code)\u003c/li\u003e\n\u003cli\u003eÜberwachung und Verbesserung der virtuellen Infrastruktur und Kundensysteme\u003c/li\u003e\n\u003cli\u003eDefinition von Anforderungen an unsere Infrastruktur sowie an unsere Software-Produkte\u003c/li\u003e\n\u003cli\u003eBetreuung und Weiterentwicklung unserer Cloud-Infrastruktur und /-services\u003c/li\u003e\n\u003cli\u003eEnge Zusammenarbeit mit unseren internationalen Kunden und Kollegen\u003c/li\u003e\n\u003cli\u003eKommunikation von Neuerungen, Strategien und Trainingsmaßnahmen an die Organisation\u003c/li\u003e\n\u003cli\u003eWeiterentwicklung von Softwarevalidierungsprozessen für Cloudanwendungen\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eWas wir von Dir erwarten\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eFundierte Kenntnisse im Bereich AWS-Services (z. B. VPC/Networking, RDS, CloudFormation, Lambda, CloudTrail, KMS, S3/EFS, EC2/Docker)\u003c/li\u003e\n\u003cli\u003eGute Kenntnisse in Python, PowerShell und/oder anderen unterstützten Scripting-Sprachen\u003c/li\u003e\n\u003cli\u003eErfahrung im Bereich IT-Security sowie Identity- und Access-Management\u003c/li\u003e\n\u003cli\u003eErfahrung mit Jenkins wünschenswert\u003c/li\u003e\n\u003cli\u003eSchnelle Auffassungsgabe sowie hohe Motivation zur kontinuierlichen Weiterentwicklung\u003c/li\u003e\n\u003cli\u003eSehr gute Deutsch- und Englischkenntnisse in Wort und Schrift\u003c/li\u003e\n\u003cli\u003eErfolgreich abgeschlossenes Studium der (Wirtschafts-) Informatik oder eine vergleichbare Qualifikation\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eWen wir suchen\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eNicht alle Anforderungen unserer Anzeige passen auf Dein Profil? Kein Problem: Überzeuge uns, dass Du dennoch der richtige Kandidat für uns bist! Neben Deinen fachlichen Fähigkeiten zählen vor allem Deine persönliche Einstellung und Deine Werte. Wir suchen Kollegen, die hohe Erwartungen an sich selbst und andere haben, sich ständig weiterentwickeln möchten und gerne Verantwortung übernehmen.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eDein Kontakt im Recruiting-Prozess\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eDu hast Fragen zum Bewerbungsprozess? Bitte zögere nicht, uns zu kontaktieren.\u003c/p\u003e\n\u003cp\u003e\u003cimg src=\"https://camo.githubusercontent.com/2c0fb211a9074412bfd878f2dacb0c4ece74fd62/68747470733a2f2f742e676f686972696e672e636f6d2f682f66333432663132346565623065373461323531643362663464313631306639333238613863376165396435373139643630353634666266303662353436383030\"\u003e\u003c/p\u003e\n",
